$(document).ready(function () {
  $(".owl-carousel").owlCarousel({
    loop: true,
    margin: 30,
    nav: true,
    dots: true,
    responsive: {
      0: {
        items: 2
      },
      768: {
        items: 3
      },
      992: {
        items: 4
      }
    }
  });



  $(window).on('scroll', function () {
    var scroll = $(window).scrollTop();

    if (scroll > 40) {
      $('.menu').addClass('active_1');
    } else {
      $('.menu').removeClass('active_1');
    }

    if (scroll > 120) {
      $('.menu').addClass('active_2');
    } else {
      $('.menu').removeClass('active_2');
    }

  });
})
